/**************************************
VIII - Lenguage Mode Toggle
**************************************/

/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 24px;
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #2B283A;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 3px;
    bottom: 2px;
    background-color: white;
    transition: .4s;
}


input:focus + .slider {
    /*checked*/
    box-shadow: 0 0 3px #A626D3;
}

input:checked + .slider {
    /*checked*/
    background-color: white;
}

input:checked + .slider:before {
    background-color: #2B283A;
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
}


.slider-text-night{
    color:#2B283A;
    color:var(--black-night);
}

.slider-text-light{
    color:rgb(166, 166, 168);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

/**************************************
IX - Dice Styles
**************************************/
.dice{
    width:48px;
    height:48px;
}

.dice-box{
    z-index:4;
}

.dice-shine {
    position: relative;
    overflow: hidden;
}
.dice-shine:after {
    content: "";
    position: absolute;
    top: -110%;
    left: -210%;
    width: 200%;
    height: 200%;
    opacity: 0;
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
    
    background: rgba(255, 255, 255, 0.13);
    background: linear-gradient(
      to right, 
      rgba(255, 255, 255, 0.13) 0%,
      rgba(255, 255, 255, 0.13) 77%,
      rgba(255, 255, 255, 0.5) 92%,
      rgba(255, 255, 255, 0.0) 100%
    );
  }
  
  /* Hover state - trigger effect */
  
  .dice-shine:hover:after {
    opacity: 1;
    top: -30%;
    left: -30%;
    transition-property: left, top, opacity;
    transition-duration: 0.7s, 0.7s, 0.15s;
    transition-timing-function: ease;
  }
  
  /* Active state */
  
  .dice-shine:active:after {
    opacity: 0;
  }


